import { updateBall } from './updateBall';
import { updatePaddle } from './updatePaddle';
import { drawBricks } from './drawBricks';
import { resetGame } from './resetGame';
import { drawMenu } from './drawMenu';
import { collisionDetection } from './collisionDetection';

export const processFrame = (
  gameState,
  deltaTime,
  canvas,
  ctx,
  x,
  y,
  dx,
  dy,
  paddleX,
  paddleHeight,
  paddleWidth,
  controls,
  bricks,
  brickColumnCount,
  brickRowCount,
  brickWidth,
  brickHeight,
  brickPadding,
  brickOffsetLeft,
  brickOffsetTop,
) => {
  ctx.clearRect(0, 0, canvas.width, canvas.height);
  switch(gameState.current) {
    case 'start':
      drawMenu(gameState, controls, ctx, canvas);
      break;
    case 'inProgress':
      updateBall(gameState, canvas, ctx, x, y, dx, dy, paddleHeight, paddleWidth, paddleX);
      updatePaddle(canvas, ctx, paddleX, paddleHeight, paddleWidth, controls);
      collisionDetection(bricks, brickWidth, brickHeight, x, y, dy);
      drawBricks(
        ctx,
        bricks,
        brickColumnCount,
        brickRowCount,
        brickWidth,
        brickHeight,
        brickPadding,
        brickOffsetLeft,
        brickOffsetTop,
      );
      break;
    case 'over':
      resetGame(
        bricks,
        brickColumnCount,
        brickRowCount,
        x,
        y,
        dx,
        dy,
        paddleX,
        paddleWidth,
        ctx,
        canvas,
        gameState,
        controls
      );
      break;
    default:
      drawMenu(gameState, controls, ctx, canvas);
      break;
  };
};
