import { resetBall } from '../resetBall';
import playgroundConstants from '../constants';

export const resetGame = (
  bricks,
  brickColumnCount,
  brickRowCount,
  x,
  y,
  dx,
  dy,
  paddleX,
  paddleWidth,
  ctx,
  canvas,
  gameState,
  controls
) => {
  const {
    startingVertAccel,
    startingHorizAccel,
  } = playgroundConstants;

  const { newX, newY } = resetBall(canvas.width, canvas.height);
  for(let c = 0; c < brickColumnCount; c++) {
    bricks[c] = [];
    for(let r = 0; r < brickRowCount; r++) {
      bricks[c][r] = { x: 0, y: 0, broken: false };
    }
  };
  x.current = newX;
  y.current = newY;
  dx.current = startingHorizAccel;
  dy.current = startingVertAccel;
  paddleX.current = (canvas.width - paddleWidth) / 2;
  ctx.font = '30px Arial';
  ctx.fillStyle = 'red';
  ctx.textAlign = 'center';
  ctx.fillText('Game over', canvas.width/2, canvas.height/2);
  ctx.font = "16px Arial";
  ctx.fillText('Press R to restart', canvas.width/2, canvas.height*0.75);
  if (controls.rKeyPressed) {
    gameState.current = 'start';
  }
};
