import React, { useRef, useEffect } from 'react';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import { useTheme } from '@material-ui/core/styles';
import { resetBall } from './resetBall';
import { processFrame } from './processFrame/processFrame';
import playgroundConstants from './constants';

const {
  canvasWidth,
  canvasHeight,
  startingVertAccel,
  startingHorizAccel,
} = playgroundConstants;

const useStyles = makeStyles(theme => ({
  link: {
    color: 'inherit',
  },
  icon: {
    marginRight: theme.spacing(2),
  },
  canvas: {
    border: '3px solid',
    marginTop: theme.spacing(8),
    marginBottom: theme.spacing(8),
    marginLeft: 'auto',
    marginRight: 'auto',
  }
}));

const useCanvasOnAnimationFrame = (canvasRef, callback) => {
  // Use useRef for mutable variables that we want to persist
  // without triggering a re-render on their change
  const requestRef = useRef();
  const previousTimeRef = useRef();
  const gameState = useRef('start');
  const x = useRef();
  const y = useRef();
  const dy = useRef(startingVertAccel);
  const dx = useRef(startingHorizAccel);
  const paddleHeight = 10;
  const paddleWidth = 75;
  const paddleX = useRef((canvasWidth - paddleWidth) / 2);

  const brickRowCount = 3;
  const brickColumnCount = 8;
  const brickWidth = 50;
  const brickHeight = 15;
  const brickPadding = 5;
  const brickOffsetTop = 30;
  const brickOffsetLeft = 20;

  const bricks = [];
  for(let c = 0; c < brickColumnCount; c++) {
    bricks[c] = [];
    for(let r = 0; r < brickRowCount; r++) {
      bricks[c][r] = { x: 0, y: 0, broken: false };
    }
  }
  const controls = {
    rightPressed: false,
    leftPressed: false,
    enterPressed: false,
    rKeyPressed: false,
  };

  let canvas, ctx;

  const animateCanvas = (time) => {
    if (previousTimeRef.current != undefined) {
      const deltaTime = time - previousTimeRef.current;
      callback(
        gameState,
        deltaTime,
        canvas,
        ctx,
        x,
        y,
        dx,
        dy,
        paddleX,
        paddleHeight,
        paddleWidth,
        controls,
        bricks,
        brickColumnCount,
        brickRowCount,
        brickWidth,
        brickHeight,
        brickPadding,
        brickOffsetLeft,
        brickOffsetTop,
      );
    }
    previousTimeRef.current = time;
    requestRef.current = requestAnimationFrame(animateCanvas);
  }

  useEffect(() => {
    const keyDownHandler = (e) => {
      switch (e.keyCode) {
        case 39:
          return controls.rightPressed = true;
        case 37:
          return controls.leftPressed = true;
        case 13:
          return controls.enterPressed = true;
        case 82:
          return controls.rKeyPressed = true;
      };
    };

    const keyUpHandler = (e) => {
      switch (e.keyCode) {
        case 39:
          return controls.rightPressed = false;
        case 37:
          return controls.leftPressed = false;
        case 13:
          return controls.enterPressed = false;
        case 82:
          return controls.rKeyPressed = false;
      };
    };

    document.addEventListener("keydown", keyDownHandler, false);
    document.addEventListener("keyup", keyUpHandler, false);
    canvas = canvasRef.current;
    ctx = canvas.getContext('2d');
    for(let c = 0; c < brickColumnCount; c++) {
      bricks[c] = [];
      for(let r = 0; r < brickRowCount; r++) {
        bricks[c][r] = { x: 0, y: 0, broken: false };
      }
    };
    const { newX, newY } = resetBall(canvas.width, canvas.height);
    x.current = newX;
    y.current = newY;
    paddleX.current = (canvas.width - paddleWidth) / 2;
    requestRef.current = requestAnimationFrame(animateCanvas);
    return () => cancelAnimationFrame(requestRef.current);
  }, []); // Make sure the effect runs only once
};

const Playground = () => {
  const theme = useTheme();
  const classes = useStyles(theme);
  const canvasRef = useRef(null);

  useCanvasOnAnimationFrame(canvasRef, processFrame);

  return (
    <main>
      <Container maxWidth="md">
        <canvas
          className={classes.canvas}
          ref={canvasRef}
          id="playground-canvas"
          width={canvasWidth}
          height={canvasHeight}
        >
        </canvas>
      </Container>
    </main>
  )
};

export default Playground;
