export const collisionDetection = (bricks, brickWidth, brickHeight, x, y, dy) => {
  bricks.forEach(brickRow => {
    brickRow.forEach(brick => {
      if(!brick.broken) {
        if(
          x.current > brick.x &&
          x.current < brick.x + brickWidth &&
          y.current > brick.y &&
          y.current < brick.y + brickHeight
        ) {
          dy.current = -dy.current;
          brick.broken = !brick.broken;
        }
      }
    });
  });
};
