import { updateSled } from "./updateSled";
import { resetGame } from "./resetGame";
import { drawMenu } from "./drawMenu";
// import { collisionDetection } from "./collisionDetection";

export const processFrame = (
  gameState,
  deltaTime,
  canvas,
  ctx,
  sledImage,
  sledX,
  sledY,
  sledDx,
  sledDy,
  sledHeight,
  sledWidth,
  controls
) => {
  ctx.clearRect(0, 0, canvas.width, canvas.height);
  switch (gameState.current) {
    case "start":
      drawMenu(gameState, controls, ctx, canvas);
      break;
    case "inProgress":
      updateSled(canvas, ctx, sledImage, sledX, sledY, sledWidth, controls);
      // collisionDetection();
      break;
    case "over":
      resetGame(
        sledX,
        sledY,
        sledDx,
        sledDy,
        sledWidth,
        sledHeight,
        ctx,
        canvas,
        gameState,
        controls
      );
      break;
    default:
      drawMenu(gameState, controls, ctx, canvas);
      break;
  }
};
