import React from 'react';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/core/styles';
import { useTheme } from '@material-ui/core/styles';
import PersonalList from '../PersonalList/PersonalList';
import { topForty2020 } from '../../../assets/lists/topForty2020';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      <Box p={3}>{children}</Box>
    </Typography>
  );
}

const useStyles = makeStyles(theme => {});

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const TopForty = () => {
  const theme = useTheme();
  const classes = useStyles(theme);
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <main>
      <Container className={classes.cardGrid} maxWidth="md">
          <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
            <Tab label="2020" {...a11yProps(0)} />
            <Tab label="2019" {...a11yProps(1)} />
            <Tab label="2018" {...a11yProps(2)} />
          </Tabs>
          <TabPanel value={value} index={0}>
            <PersonalList listMetadataArray={topForty2020} />
          </TabPanel>
          <TabPanel value={value} index={1}>
            Still need to upload this elsewhere, it's blocked on YouTube.
          </TabPanel>
          <TabPanel value={value} index={2}>
            Still need to upload this elsewhere, it's blocked on YouTube.
          </TabPanel>
      </Container>
    </main>
  )
}

export default TopForty;
