import React, { useState } from "react";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Link from "@material-ui/core/Link";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import { useTheme } from "@material-ui/core/styles";

import { actualGiftList } from "./actualGiftList";
import { themeList } from "./themeList";
import { dreamList } from "./dreamList";

const useStyles = makeStyles((theme) => ({
  activeButton: {
    transition: "all 0.2s ease-in",
    backgroundColor: "yellow",
    color: "red",
    "&:hover": {
      backgroundColor: "red",
      color: "yellow",
    },
  },
  restButton: {
    backgroundColor: "white",
  },
  box: {
    display: "flex",
    justifyContent: "center",
    marginBottom: theme.spacing(3),
  },
  link: {
    color: "inherit",
  },
  icon: {
    marginRight: theme.spacing(2),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  cardMedia: {
    paddingTop: "112.5%", // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
}));

const THINGS = "things";
const THEMES = "themes";
const DREAMS = "dreams";

const getCurrentList = (section) => {
  switch (section) {
    case THINGS:
      return actualGiftList;
    case THEMES:
      return themeList;
    case DREAMS:
      return dreamList;
  }
};

const GiftList = () => {
  const [section, setSection] = useState(THINGS);

  const list = getCurrentList(section);
  const theme = useTheme();
  const classes = useStyles(theme);
  return (
    <main>
      <Container className={classes.cardGrid} maxWidth="md">
        <Box className={classes.box}>
          <ButtonGroup size="large">
            <Button
              className={
                section === THINGS ? classes.activeButton : classes.restButton
              }
              variant="outlined"
              onClick={() => setSection(THINGS)}
            >
              Things
            </Button>
            <Button
              className={
                section === THEMES ? classes.activeButton : classes.restButton
              }
              variant="outlined"
              onClick={() => setSection(THEMES)}
            >
              Themes
            </Button>
            <Button
              className={
                section === DREAMS ? classes.activeButton : classes.restButton
              }
              variant="outlined"
              onClick={() => setSection(DREAMS)}
            >
              Dreams
            </Button>
          </ButtonGroup>
        </Box>
        <Grid container spacing={4}>
          {list.map((gift, index) => {
            if (gift.name !== "") {
              return (
                <Grid item key={index} xs={12} sm={6} md={4}>
                  <Card className={classes.card}>
                    <CardMedia
                      className={classes.cardMedia}
                      image={gift.imgSrc}
                      title={gift.name}
                    />
                    <CardContent className={classes.cardContent}>
                      <Typography
                        color="secondary"
                        align="left"
                        variant="h5"
                        component="h2"
                      >
                        <Link href={gift.purchaseHref} className={classes.link}>
                          {gift.name}
                        </Link>
                      </Typography>
                      <Typography variant="h6" component="h3">
                        {gift.type}
                      </Typography>
                      <Typography variant="body1">{gift.price}</Typography>
                      <Typography variant="body2">{gift.notes}</Typography>
                    </CardContent>
                  </Card>
                </Grid>
              );
            }
          })}
        </Grid>
      </Container>
    </main>
  );
};

export default GiftList;
