export const updatePaddle = (
  canvas,
  ctx,
  paddleX,
  paddleHeight,
  paddleWidth,
  controls,
) => {
  const {
    leftPressed,
    rightPressed
  } = controls;

  if(rightPressed) {
    paddleX.current += 7;
    if (paddleX.current + paddleWidth > canvas.width){
      paddleX.current = canvas.width - paddleWidth;
    }
  } else if(leftPressed) {
    paddleX.current -= 7;
    if (paddleX.current < 0){
      paddleX.current = 0;
    }
  }
  ctx.beginPath();
  ctx.rect(paddleX.current, canvas.height - paddleHeight, paddleWidth, paddleHeight);
  ctx.fillStyle = "#000000";
  ctx.fill();
  ctx.closePath();
};
