import sledSprite from "../../../../assets/sled1.png";

export const updateSled = (
  canvas,
  ctx,
  sledImage,
  sledX,
  sledY,
  sledWidth,
  controls
) => {
  const { leftPressed, rightPressed } = controls;

  if (rightPressed) {
    sledX.current += 7;
    if (sledX.current + sledWidth > canvas.width) {
      sledX.current = canvas.width - sledWidth;
    }
  } else if (leftPressed) {
    sledX.current -= 7;
    if (sledX.current < 0) {
      sledX.current = 0;
    }
  }

  ctx.drawImage(
    sledImage,
    sledX.current,
    canvas.height - canvas.height / 2,
    sledImage.width / 5,
    sledImage.height / 5
  );
};
