import React, { useRef, useEffect } from "react";
import Container from "@material-ui/core/Container";
import { makeStyles } from "@material-ui/core/styles";
import { useTheme } from "@material-ui/core/styles";
import sledSprite from "../../../assets/sled1.png";

import { processFrame } from "./processFrame/processFrame";
import { constants } from "./constants";

const {
  canvasWidth,
  canvasHeight,
  startingVertAccel,
  startingHorizAccel,
} = constants;

const useStyles = makeStyles((theme) => ({
  link: {
    color: "inherit",
  },
  icon: {
    marginRight: theme.spacing(2),
  },
  canvas: {
    border: "3px solid",
    marginTop: theme.spacing(8),
    marginBottom: theme.spacing(8),
    marginLeft: "auto",
    marginRight: "auto",
  },
}));

const useCanvasOnAnimationFrame = (canvasRef, processFrame) => {
  // Use useRef for mutable variables that we want to persist
  // without triggering a re-render on their change
  const requestRef = useRef();
  const previousTimeRef = useRef();
  const gameState = useRef("start");
  const sledHeight = 50;
  const sledWidth = 75;
  const sledY = useRef((canvasHeight - sledHeight) / 2);
  const sledX = useRef((canvasWidth - sledWidth) / 2);
  const sledDy = useRef(startingVertAccel);
  const sledDx = useRef(startingHorizAccel);

  const controls = {
    rightPressed: false,
    leftPressed: false,
    upPressed: false,
    ldownPressed: false,
    enterPressed: false,
    rKeyPressed: false,
  };

  let canvas, ctx, sledImage;

  const animateCanvas = (time) => {
    if (previousTimeRef.current != undefined) {
      const deltaTime = time - previousTimeRef.current;
      processFrame(
        gameState,
        deltaTime,
        canvas,
        ctx,
        sledImage,
        sledX,
        sledY,
        sledDx,
        sledDy,
        sledHeight,
        sledWidth,
        controls
      );
    }
    previousTimeRef.current = time;
    requestRef.current = requestAnimationFrame(animateCanvas);
  };

  // add up and down
  useEffect(() => {
    sledImage = new Image();
    sledImage.src = sledSprite;
    const keyDownHandler = (e) => {
      switch (e.keyCode) {
        case 39:
          return (controls.rightPressed = true);
        case 37:
          return (controls.leftPressed = true);
        case 13:
          return (controls.enterPressed = true);
        case 82:
          return (controls.rKeyPressed = true);
      }
    };

    const keyUpHandler = (e) => {
      switch (e.keyCode) {
        case 39:
          return (controls.rightPressed = false);
        case 37:
          return (controls.leftPressed = false);
        case 13:
          return (controls.enterPressed = false);
        case 82:
          return (controls.rKeyPressed = false);
      }
    };

    document.addEventListener("keydown", keyDownHandler, false);
    document.addEventListener("keyup", keyUpHandler, false);
    canvas = canvasRef.current;
    ctx = canvas.getContext("2d");
    sledX.current = (canvas.width - sledWidth) / 2;
    requestRef.current = requestAnimationFrame(animateCanvas);
    return () => cancelAnimationFrame(requestRef.current);
  }, []); // Make sure the effect runs only once
};

const SledGame = () => {
  const theme = useTheme();
  const classes = useStyles(theme);
  const canvasRef = useRef(null);

  useCanvasOnAnimationFrame(canvasRef, processFrame);

  return (
    <main>
      <Container maxWidth="md">
        <canvas
          className={classes.canvas}
          ref={canvasRef}
          id="sled-canvas"
          width={canvasWidth}
          height={canvasHeight}
        ></canvas>
      </Container>
    </main>
  );
};

export default SledGame;
